.channels-container {
  margin: 5px 10px;
}

.channel-group {
  float: left;
}

.channel-levels > ul {
  width: 212px !important;
}

.led-bar {
  float: left;
}

.led-bar button {
  pointer-events:none;
  margin: 1px;
  cursor: default;
  width: 14px;
  height: 36px;
  float: left;
  padding: 0;
}

@media (max-width: 767px) {
  .channels-container {
    display: block;
  }
}

@media (min-width: 420px) {
  .channel-levels > .dropdown-menu {
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg);
    left: 100px;
    top: -50px;
  }

  .responsive-rotate-90 {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
  }

  #mute-all .glyphicon {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
  }
}

.number-param-container {
  display: table;
  width: 100%;
  margin-bottom: -20px;
}

.min-number, .slider, .max-number {
  display: table-cell;
  text-align: center;
}

.min-number, .max-number {
  width: 34px;
  vertical-align: top;
}

.slider-container {
  margin: 12px 24px 0;
}

.slider > h6, .rc-slider-mark-text{
  font-size: 12px;
  color: #999;
  margin-top: 6px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


body {
  padding-top: 70px;
  padding-bottom: 50px;
}

.block-ui {
  position: relative;
  min-height: 3em;
}

.block-ui-container {
  position: absolute;
  z-index: 1010;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  min-height: 2em;
  cursor: not-allowed;
  overflow: hidden;
}

.block-ui-container:focus {
  outline: none;
}

.block-ui-overlay {
  width: 100%;
  height: 100%;
}

.content-loader {
  margin-top: 30px;
}

.content-loader h5 {
  margin-top: 20px;
}

.line-scale > div {
  width: 30px;
  height: 144px;
  border-radius: 10px;
  margin: 5px;
}

*:focus {
  outline: none !important
}

.panel-body {
  padding: 15px 15px 5px !important;
}

.navbar-nav > li > .dropdown-menu {
  border: 1px solid rgba(0, 0, 0, 0.6);
}

.end-button {
  width: 25%;
}

.end-button > li {
  width: 100%;
  text-align: center;
}

.middle-buttons {
  width: 50%;
}

.middle-buttons > li {
  width: 50%;
  text-align: center;
}

.middle-buttons > li > a {
  font-weight: bold;
}

.device-name {
  float: left;
  margin-right: 15px;
}

.form-header {
  margin-top: 0;
  font-weight: bold;
}

.navbar-nav {
  margin: 0 !important;
  float: left;
}

.navbar > .container-fluid {
  margin-right: -15px !important;
  margin-left: -15px !important;
}

.nav > li {
  float: left !important;
}

.navbar-nav > li > a {
  padding-top: 15px !important;
  padding-bottom: 15px !important;
}

.navbar-nav > li > .dropdown-menu {
  position: absolute !important;
  background-color: #3a3f44 !important;
}

.navbar-right {
  float: right !important;
}

.navbar-right .dropdown-menu {
  left: auto !important;
  right: 0 !important;
}

.middle-buttons > li.active > a {
  background-image: -webkit-linear-gradient(#78cc78, #62c462 60%, #53be53);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#78cc78), color-stop(60%, #62c462), to(#53be53));
  background-image: linear-gradient(#78cc78, #62c462 60%, #53be53);
  background-repeat: no-repeat;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff78cc78', endColorstr='#ff53be53', GradientType=0);
  -webkit-filter: none;
  filter: none;
}

.middle-buttons > li.active > a:hover {
  background-image: -webkit-linear-gradient(#2f7d2f, #379337 40%, #3a9a3a);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#2f7d2f), color-stop(40%, #379337), to(#3a9a3a));
  background-image: linear-gradient(#2f7d2f, #379337 40%, #3a9a3a);
  background-repeat: no-repeat;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff2f7d2f', endColorstr='#ff3a9a3a', GradientType=0);
  -webkit-filter: none;
  filter: none;
}

.end-button > li.open > a {
  color: #62c462 !important;
}

.recharts-default-tooltip {
  background-color: #272b30 !important;
}

.Toastify__toast-body {
  margin: auto 15px;
}

